
.App {
  position: absolute;
  width: 100%;
  height: 100%;
}

.App:active #shuffle {
  width: 38%;
}

.App:active #press {
  animation: rotating 0.5s linear infinite;

  width: 50%;
  height: auto;
  top: -20px;
  left: -22px;
  transition-property: width;
  transition-duration: 2s;
  transition-timing-function: linear;
  transition-delay: 1s;
}


html {
  cursor: none;
  overflow: hidden;
}
.pointer {
  position: absolute;
  height: 350px; top: 0px;
  width: auto; left: 0px;
  pointer-events: none;
}


.pointer img {
  width: 140px;
}

#press {
  position: absolute;
  height: 60%; top: -35px;
  width: auto; left: -40px;
  pointer-events: none;
}



figure {
  margin: 0px;
  padding: 0px;
}


#principal {
  width: auto;
  height: auto;
  z-index: 2;
  margin-top: 6%;
  max-width: 100%;
  max-height: 100%;
  display: block;
  box-shadow: 0px 8px 20px 0.1px #cfcfcf;
}


#imgcontainer {
  width: 100%;
  height: 800px;
  /* max-height: 900px; */
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;

}

.circulo {
  width: 20%;
  height: auto;
  position: absolute;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rodar {
  -webkit-animation: rotating 150s linear infinite;
  -moz-animation: rotating 150s linear infinite;
  -ms-animation: rotating 150s linear infinite;
  -o-animation: rotating 150s linear infinite;
  animation: rotating 150s linear infinite;
  z-index: -1;
  width: 17%;
  height: auto;
}

#circulo1 {
  left: 22%;
  top: 41%;
}

#circulo2 {
  left: 60%;
  top: 15%;
}

#texto {
  position: absolute;
  top: 10%;
  left: 0px;
}

button {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  color: black;
  font-family: monospace;
  font-size: 4rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: #fff;
}

#next {
  position: absolute;
  top: 70%;
  left: 77%;
}

#previous {
  position: absolute;
  top: 75%;
  left: -5%;
  transform: rotate(270deg);
}

button:hover {
  font-style: italic;
  font-size: 5.5em;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  transform: scale(0.99);
}

p {
  position: absolute;
  top: 90%;
  left: 60%;
  font-family: monospace;
}


